@font-face {
  font-family: 'live-the-life';
  src:
    url('asset/font/live-the-life.ttf?olutk4') format('truetype'),
    url('asset/font/live-the-life.woff?olutk4') format('woff'),
    url('asset/font/live-the-life.svg?olutk4#live-the-life') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: Quicksand;
  src: 
    url('asset/font/quicksand.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

%super-happy {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'live-the-life' !important;
  // speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="live-the-life-"], [class*=" live-the-life-"] {
  @extend %super-happy;
}

